<template>
    <div class="topall">
        <div class="top">
            <router-link to="/main/home" tag="i">
                <i class="iconfont icon-fanhui"></i>
            </router-link>
            <router-link to="/me/list" tag="div">
                <i class="iconfont icon-sousuo"></i>
                <span>{{search2.frontword}}</span>
            </router-link>
            <span>搜索 </span>
        </div>
        <div class="searchlist" v-for="(v,i) of search1" :key="i">
            <div>{{v.frontword}}</div>
        </div>
    </div>
</template>
<script>
    import Bscroll from "better-scroll"
    import jsonp from "fetch-jsonp"

    export default{
        data(){
            return {
                search1:[],
                search2:""
            }
        },
        methods:{
            parseParams(params){
                let paramsStr = "";
                for (const key in params) {
                    paramsStr +=`${key}=${params[key]}&`
                }
                return paramsStr.substring(0,paramsStr.length-1);
            },
            loadsearch(){
                let url = "https://list.mogujie.com/module/mget";
                let params = this.parseParams({                  
                    platform:"H5",
                    code:"hotWord,sketch"
                });
                jsonp(url+"?"+params).then((response)=>{
                    return response.json();
                }).then(({data})=>{
                    console.log(1);
                    console.log(data);
                    this.search1 = data.hotWord.data;    
                    this.search2 = data.sketch.data;                
                })
            }
        },
        mounted(){
            this.loadsearch();
        }
    }
</script>
<style lang="less">
    .topall{
        display: flex;
        // justify-content: center;
        flex-wrap: wrap;
         .top{
        // border: 1px solid #000;
            background-color: white;
            line-height: 0.82rem;
            width: 100%;
            height: 0.82rem;
            display: flex;
            justify-content: center;
            position: fixed;
            top: 0;
            left: 0;
            div{
                color: #ccc;
                padding-left: 0.1rem;
                line-height: 0.52rem;
                margin-top: 0.15rem;
                background-color: #eee;
                border-radius: 0.1rem;
                width: 5rem;
                height: 0.52rem;
            }
        }
        .iconfont{
            flex-grow: 1;
            text-align: center;
            font-size: 0.45rem;
        }
        .searchlist{
                // display: flex;
                // justify-content: center;
                text-align: center;
                margin-top: 1rem;
                width: 20%;
                height: 0.5rem;
                line-height: 0.5rem;
                border: 1px solid #eee;
                margin-left: 0.3rem;
                margin-top: 0.3rem;
                border-radius: 0.1rem;
            }
    }
   

</style>



