<template> 
        <div class="homelikes">
            <div class="font">猜你喜欢</div>
            <div class="homelikesmain">
                <div class="home" v-for="(v,i) of homeLikes" :key="i">
                    <img :src="v.img">
                    <p class="title">{{v.title}}</p>
                    <p class="price">¥{{v.price}}</p>
                </div>
            </div>
        </div>
</template>
<script>
    import jsonp from "fetch-jsonp"

    export default{
        data(){
            return {
                homeLikes:[]
            }
        },
        methods:{
            parseParams(params){
                let paramsStr = "";
                for (const key in params) {
                    paramsStr +=`${key}=${params[key]}&`
                }
                return paramsStr.substring(0,paramsStr.length-1);
            },
            loadhomeLikes(){
                let url = "https://list.mogujie.com/search";
                let params = this.parseParams({                  
                    sort:"pop",
                    pid:"9750",
                    cKey:46
                });
                jsonp(url+"?"+params).then((response)=>{
                    return response.json();
                }).then(({result})=>{
                    this.homeLikes = result.wall.docs;                    
                })
            }
        },
        mounted(){
            this.loadhomeLikes();
        }
    }
</script>
<style lang="less">
    .homelikes{
        // border: 1px solid #000;
        width:  100%;
        // height: 20rem;
        background-color: white;
        // display: flex;
        // justify-content: space-around;
        .font{
            padding-top: 0.3rem;
            padding-left: 0.2rem;
            background-image: url(../../static/img/img.png);
            background-repeat: no-repeat;
            background-size: 80%;
            background-position-y: 80%;
            background-position-x: 55%;
            text-align: center;
        }
        .homelikesmain{
        // border: 1px solid yellow;
        // font-size: 0.23rem;
        display: flex;
        justify-content: space-around;
        width:  100%;
        // height: 20rem;
        background-color: white;
        // text-align: center;
        flex-wrap: wrap;
        margin-top: 0.3rem;
        // padding-top: 0.2rem;

         }
        .home{
            // background-color: red;
            // border: 1px solid #000;
            width: 45%;
            height: 5.6rem;
            // display: flex;
            // justify-content: space-around;
            img{
                // border: 1px solid #000;
                border-radius: 0.1rem;                
                width: 2.8rem;
                height: 4rem;
            }
            .title{
                font-size: 0.25rem;
                height:0.65rem;
                color: rgb(55, 79, 107);
                overflow: hidden;
            }
            .price{
                font-size: 0.5rem;
            }
        }
    }
    
</style>



