<template>
    <div class="mh-body wrapper" ref="wrapper">
        <div class="content">
            <MainHomeSwiper></MainHomeSwiper>
            <MainHomeTypes></MainHomeTypes> 
            <MainHomeBerefit></MainHomeBerefit>
            <MainHomeHotTypes></MainHomeHotTypes>
            <MainHomeLikes></MainHomeLikes>
        </div>
    </div>
</template>
<script>
import Bscroll from "better-scroll"
import jsonp from "fetch-jsonp"
import MainHomeSwiper from "./MainHomeSwiper"
import MainHomeTypes from "./MainHomeTypes"
import MainHomeBerefit from "./MainHomeBerefit"
import MainHomeHotTypes from "./MainHomeHotTypes"
import MainHomeLikes from "./MainHomeLikes"



    export default{
        components:{
            MainHomeSwiper,
            MainHomeTypes,
            MainHomeBerefit,
            MainHomeHotTypes,
            MainHomeLikes
        },
        data(){
            return{

            }
        },
        methods:{
            bscrollInit(){
                this.$nextTick(()=>{
                    if(!this.scroll){
                        this.scroll=new Bscroll(this.$refs.wrapper,{});
                    }else{
                        this.scroll.refresh();
                    }
                });
            }
        },
        parseParams(){
            let paramsStr="";
            for(const key in params){
                paramsStr +=`${key}=${params[key]}&`
            }
            return paramsStr.substring(0,paramsStr.length-1);
        },
        loadHomeDatas(){
            let url = "https://mce.mogucdn.com/jsonp/multiget/3";
            let params = this.parseParams({
                appPlat:"m",
                pids:"51822,106930,51833,51836"
            });
            jsonp(url+"?"+params).then((response)=>{
                if(response.ok){
                    return response.json();
                }else{
                    return{
                        returnCode:false,
                        msg:"请求失败"
                    }
                }
            }).then(({data})=>{
                console.log(data);
                this.wrapImgs=data["51822"]["list"];
                this.types=data["106930"]["list"];
                this.bebfit=data["51833"]["list"];
                this.hotTypes=data["51836"]["list"];
            })
        },
        mounted(){
            this.bscrollInit();
            // this.loadHomeDatas();
        }
    }
        
</script>
<style lang="less">
    .mh-body{
        margin-top: 0.05rem;
        background-color:#eee;
        position: absolute;
        top:0.8rem;
        left: 0rem;
        right: 0rem;
        bottom: 0.8rem;
    }
    .smalltypes{
        margin-top: -0.18rem;
    }
</style>



