import jsonp from "fetch-jsonp"
function parseParams(params){
    let paramsStr = "";
    for (const key in params) {
        paramsStr +=`${key}=${params[key]}&`
    }
    return paramsStr.substring(0,paramsStr.length-1);
}

const actions={
    // 远端加载数据
    loadWarpImgs(store){
        let url = "https://mce.mogucdn.com/jsonp/multiget/3";
        let params = parseParams({
            appPlat:"m",
            pids:"51822"
        });
        jsonp(url+"?"+params).then((response)=>{
            return response.json();
        }).then(( { data } )=>{
            // this.warpImgs = data["51822"]["list"];
            store.commit("setWarpImgs",data["51822"]["list"]);
        })
    },
    loadWarpTypes(store){
        let url = "https://mce.mogucdn.com/jsonp/multiget/3";
        let params = parseParams({
            appPlat:"m",
            pids:"106930"
        });
        jsonp(url+"?"+params).then((response)=>{
            return response.json();
        }).then(( { data } )=>{
            store.commit("setWarpTypes",data["106930"]["list"]);           
            // this.warpTypes = data["106930"]["list"];
        })
    },
    loadberefit(store){
        let url = "https://mce.mogucdn.com/jsonp/multiget/3";
        let params = parseParams({
            appPlat:"m",
            pids:"51833"
        });
        jsonp(url+"?"+params).then((response)=>{
            return response.json();
        }).then(( { data } )=>{
            store.commit("setBerefit",data["51833"]["list"]);    
            // this.berefit = data["51833"]["list"];
        })
    }, 
    loadhotTypes(store){
        let url = "https://mce.mogucdn.com/jsonp/multiget/3";
        let params = parseParams({
            appPlat:"m",
            pids:"51836"
        });
        jsonp(url+"?"+params).then((response)=>{
            return response.json();
        }).then(( { data } )=>{
            store.commit("setHotTypes",data["51836"]["list"]);                
            // this.hotTypes = data["51836"]["list"];
        })
    }



}

export default actions;


