const mutations ={
    setWarpImgs(state,data){
        state.warpImgs = data;
    },
    setWarpTypes(state,data){
        state.warpTypes = data;        
    },
    setBerefit(state,data){
        state.berefit = data;        
    },
    setHotTypes(state,data){
        state.hotTypes = data;        
    },
    setPid(state,data){
        state.pid=data;
    },
    setFcid(state,data){
        state.fcid=data;
    },
    setSort(state,data){
        state.sort=data;
    },
    setSortDetails(state,data){
        state.sortDetails=data;
    },
    setListDetails(state,data){
        state.listDetails=data;
    }

}

export default mutations;


