<template>
    <div class="typeall">
        <div class="typeleft">
            <div class="wrapper" ref="wrap1">
                <div class="content">
                    <div class="leftlist" v-for="(v,i) of leftlist" :key="i" @click="onClick(v.maitKey,i,v.miniWallkey)" :class="{act:i==num}">
                        <p>{{v.title}}</p>
                    </div>
                </div>  
            </div>
        </div>
        
        <div class="typeright">
            <div class=" wrapper" ref="wrap2">
                <div class="content a">
                    <div class="rightlist1" v-for="(v,i) of rightlist" :key="i">
                        <img :src="v.image">
                        <p>{{v.title}}</p>
                    </div>
                    <div class="rightlist2">
                        <div class="fonts">
                            <div v-for="(v,i) of sortDetails" :class="{ch:true ,acts:num2==i}" @click="change(v.sortKey,i)" :key="i">
                                {{ v.title }}
                            </div>
                        </div>
                        <div class="fontsalllist">
                            <div class="fontsall" v-for="(v,i) of listDetails" :key="i">
                                <div class="imglist">
                                    <img :src="v.img">
                                </div>
                                <p class="firstp">{{v.title}}</p>
                                <p class="secondp">¥{{v.price}}<span>{{v.sale}}<i class="iconfont icon-shoucang"></i></span></p>
                            </div>
                        </div>
                    </div>
                </div> 
            </div>
        </div>
    </div>
</template>
<script>
    import Bscroll from "better-scroll"
    import jsonp from "fetch-jsonp"

    export default{
        data(){
            return {
                leftlist:[],
                rightlist:[],
                num:0,
                num2:0
                // pid:null
            }
        },
        // watch:{
        //     pid(newPid){
        //         this.loadrightlist(newPid);
        //     }
        // },
        computed:{
            pid(){
                return this.$store.getters.pid;
            },
            ficd(){
                return this.$store.getters.fcid;
            },
            sort(){
                return this.$store.getters.sort;
            },
            sortDetails(){
                return this.$store.getters.sortDetails;
            },
            listDetails(){
                return this.$store.getters.listDetails;
            }
        },
        methods:{
            bscrollInit(a){
                this.$nextTick(()=>{
                    if(!this[a]){
                        this[a]=new Bscroll(this.$refs[a],{
                            click:true
                        });
                    }else{
                        this[a].refresh();
                    }
                });
            },
            bscrollInit(b){
                this.$nextTick(()=>{
                    if(!this[b]){
                        this[b]=new Bscroll(this.$refs[b],{
                            click:true
                        });
                    }else{
                        this[b].refresh();
                    }
                });
            },
            parseParams(params){
                let paramsStr = "";
                for (const key in params) {
                    paramsStr +=`${key}=${params[key]}&`
                }
                return paramsStr.substring(0,paramsStr.length-1);
            },
            loadleftlist(){
                let url = "https://mce.mogucdn.com/jsonp/multiget/3";
                let params = this.parseParams({
                    appPlat:"m",
                    pids:"41789"
                });
                jsonp(url+"?"+params).then((response)=>{
                    return response.json();
                }).then(( { data } )=>{
                    this.leftlist = data["41789"]["list"];
                    this.$store.commit("setPid",this.leftlist[0].maitKey)
                    this.$store.commit("setFcid",this.leftlist[0].miniWallkey)
                    this.loadrightlist(this.pid)
                    this.loadlistbootom()
                })
            },
            loadrightlist(pid){
                let url = "https://mce.mogujie.com/jsonp/makeup/3";
                let params = this.parseParams({
                    appPlat:"m",
                    pid:pid
                });
                jsonp(url+"?"+params).then((response)=>{
                    return response.json();
                }).then(( { data } )=>{
                    console.log(data)
                    console.log(data.categoryNavigation.list);
                   this.rightlist = data.categoryNavigation.list;
                })
            },
            loadlistbootom(){
                let url = "https://list.mogujie.com/search";
                let params = this.parseParams({
                    fcid:this.fcid,
                    page:1,
                    sort:this.sort,
                    cKey:"h5-cube"
                });
                jsonp(url+"?"+params).then((response)=>{
                    return response.json();
                }).then((  {result}  )=>{
                    console.log(1);
                    console.log(result);
                    this.$store.commit("setSort",result.sortFilter[0].sortKey);this.$store.commit("setSortDetails",result.sortFilter);
                    this.$store.commit("setListDetails",result.wall.docs);
                })
            },
            onClick(id,n,id2){
                this.$store.commit("setPid",id);
                this.num =n;
                this.loadrightlist(id);
                this.$store.commit("setFcid",id2);
                this.loadlistbootom(id2);
            },
            change(id,i){
                this.$store.commit("setSort",id);
                this.loadlistbootom();
                this.num2=i;
            }
        },
        mounted(){
            this.loadleftlist();         
            this.bscrollInit("wrap1");
            this.bscrollInit("wrap2");
        }
    }
</script>
<style lang="less">
    .typeall{
        display: flex;
        .typeleft{
            text-align: center;
            .wrapper{
                width: 20%;
                position: absolute;
                top: 0.8rem;
                bottom: 0.8rem;
                background-color: #eee;
            }
            .act{
                color: red;
                background-color: white;
            }
            p{
                padding-top: 0.15rem;
                padding-bottom: 0.15rem;
            }
        }
        .typeright{
            text-align: center;
            .wrapper{
                margin-left: 20%;
                width: 80%;
                position: absolute;
                top: 0.8rem;
                bottom: 0.8rem;
                .a{
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-around;
                    .rightlist1{
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-around;
                    width: 30%;
                    height: 2.2rem;
                    // border: 1px solid red;
                    img{
                        width: 1.5rem;
                        height: 1.5rem;
                    }
                }
                    .rightlist2{
                        width: 90%;
                        height: 100%;
                        .fonts{
                            padding-top: 0.1rem;
                            padding-bottom: 0.1rem;
                            border: 1px solid #eee;
                            display: flex;
                            justify-content: space-around;
                            margin-bottom: 0.15rem;
                        }
                        .fontsalllist{
                            display: flex;
                            justify-content: space-around;
                            flex-wrap: wrap;
                            width: 100%;
                            height: 100%;
                            .fontsall{
                                width: 46%;
                                height: 4rem;
                                .imglist{
                                    width: 100%;
                                    height: 2.5rem;
                                    img{
                                        width: 100%;
                                        border-radius: 0.1rem;
                                        height: 100%;
                                    }
                                }
                            }
                            .firstp{
                                width: 100%;
                                height: 0.75rem;
                            }
                            .secondp{
                                // border: 1px solid #000;
                                text-align: left;
                                margin-left: 0.2rem;
                                font-size: 0.4rem;
                                span{
                                    font-size: 0.15rem;
                                    float: right;
                                    text-align: center;
                                    // border: 1px solid #000;
                                    line-height: 0.5rem;
                                }
                            }
                        }
                        .acts{
                            color:red;
                        }
                    }
                }
            }
        }
    }
</style>



