import Vue from 'vue'
import Router from 'vue-router'

import MainContent from '@/components/MainContent'
import SimpleContent from '@/components/SimpleContent'
import MeContent from '@/components/MeContent'

import MainHome from '@/components/MainHome'
import MainTypeList from '@/components/MainTypeList'
import Search from '@/components/Search'
import MainHomeSwiper from '@/components/MainHomeSwiper'
import ShoppingList from '@/components/ShoppingList'
import MeList from '@/components/MeList'




Vue.use(Router)

export default new Router({
  mode:"history",
  linkExactActiveClass:"active",
  routes: [
    {path:"/",redirect:"/main"},
    {path: '/main',name:'main',component:MainContent,children:[
      {path:"",redirect:"home"},
      {path: 'home',name:'home',component:MainHome},
      {path: 'types',name:'types',component:MainTypeList},
      {path: 'search',name:'search',component:Search},
      {path: 'shopp',name:'shopp',component:ShoppingList} 
      // {path: 'types',name:'types',component:ShoppingList},    
    ]},
    {path: '/simple',name:'simple',component:SimpleContent,children:[
      {path: 'shop',name:'shop',component:ShoppingList}
    ]},
    {path: '/me',name:'me',component:MeContent,children:[
      {path: 'list',name:'list',component:MeList}
    ]}
  ]
})



