const getters={
    warpImgs(state){
        return state.warpImgs;
    },
    warpTypes(state){
        return state.warpTypes;
    },
    berefit(state){
        return state.berefit;
    },
    hotTypes(state){
        return state.hotTypes;
    },
    fcid(state){
        return state.fcid;
    },
    pid(state){
        return state.pid;
    },
    sort(state){
        return state.sort;
    },
    sortDetails(state){
        return state.sortDetails;
    },
    listDetails(state){
        return state.listDetails;
    }
}

export default getters;


